<template>
	<dt-scroll-box>
		<div slot="top">
			<div class="dt-page-top clearfix" style="margin-bottom: 30px;">
				<span class="dt-bread-item">创新工程-先导专项</span>
				<div class="btn-box fr">
					<el-button class="dt-btn green-btn" @click="handleFillBudget">填写预算书</el-button>
					<el-button class="dt-btn" @click="exportModel">导出模板</el-button>
					<el-button class="dt-btn"><i class="iconfont icons-daoru"></i>导入</el-button>
					<el-button class="dt-btn" @click="exportModel">生成PDF</el-button>
					<el-button class="dt-btn" @click="handleSave"><i class="iconfont icons-baocun"></i>保存</el-button>
					<el-button class="dt-btn" @click="handleSubmit"><i class="iconfont icons-tijiao"></i>提交</el-button>
				</div>
			</div>
			<ul class="dt-tab clearfix">
				<li class="dt-tab-item fl" 
					v-for="(item,idx) in tabData"
					:key="idx" 
					@click="handleTabClick(idx)"
					:class="activeIdx === idx? 'active':''">{{item}}</li>
			</ul>
		</div>
		<div class="inovate-forerunner-page" slot="main-content" v-loading="GlobalLoadingFlag">
			<el-form :model="formData" :rules="rules" class="demo-form-inline" label-width="124px" v-if="!GlobalLoadingFlag">
				<!-- 基础信息 -->
				<div v-show="activeIdx === 0" class="basic-info">
					<div class="pro-no">申报编号：{{projectNo}}</div>
					<h5 class="sub-title"><span class="bar"></span>任务基本信息</h5>
					<div class="flex-box width-80">
						<div class="flex-item1">
							<el-form-item label="任务名称：" prop="projectName">
					    		<el-input v-model="formData.projectName" placeholder="请输入"></el-input>
					  		</el-form-item>
						</div>
						<div class="flex-item1">
							<el-form-item label="拟申请经费" prop="applyFunds">
					    		<el-input type="number" disabled v-model="formData.applyFunds" placeholder="请输入">
					    			<i slot="suffix" class="pr5">万元</i>
					    		</el-input>
					  		</el-form-item>
						</div>
						<div class="flex-item1">
							<el-form-item label="拟申请岗位数" prop="applyPostNumber">
					    		<el-input type="number" v-model="formData.applyPostNumber" placeholder="请输入"></el-input>
					  		</el-form-item>
						</div>
					</div>
					<div class="flex-box width-80">
						<div class="flex-item2">
							<el-form-item label="预期成果类型：" prop="achievementType">
						    	<el-select v-model="formData.achievementType" class="max-width-no" multiple placeholder="请选择">
								    <el-option
								      v-for="item in PRO_RELUST_TYPE"
								      :key="item.id"
								      :label="item.label"
								      :value="item.id">
								    </el-option>
								</el-select>
						  	</el-form-item>
						</div>
						<div class="flex-item1"></div>
					</div>
					<el-form-item label="任务执行期限：">
				    	<el-date-picker
					      v-model="deadline"
					      type="daterange"
					      @change="handleDeadlineChange"
					      range-separator="至"
					      start-placeholder="开始日期"
					      end-placeholder="结束日期">
					    </el-date-picker>
				  	</el-form-item>
				 	<h5 class="sub-title mt10"><span class="bar"></span>任务承担单位信息</h5>
					<div class="flex-box width-80">
						<div class="flex-item2">
							<el-form-item label="单位名称：" prop="leadDept.deptName">
						    	<el-input class="max-width-2" v-model="formData.leadDept.deptName" placeholder="请输入"></el-input>
						  	</el-form-item>
						  	<el-form-item label="通讯地址：" prop="leadDept.address">
						    	<el-input class="max-width-2" v-model="formData.leadDept.address" placeholder="请输入"></el-input>
						  	</el-form-item>
						  	<div class="flex-box">
						  		<el-form-item class="flex-item1" label="联系人：" prop="leadDept.contactsName">
							    	<el-input v-model="formData.leadDept.contactsName" placeholder="请输入"></el-input>
							  	</el-form-item>
							  	<el-form-item class="flex-item1" label="联系电话：">
							    	<el-input v-model="formData.leadDept.mobile" placeholder="请输入"></el-input>
							  	</el-form-item>
						  	</div>
						</div>
						<div class="flex-item1">
							<el-form-item label="邮政编码：" prop="leadDept.zipCode">
							    <el-input v-model="formData.leadDept.zipCode" placeholder="请输入"></el-input>
							</el-form-item>
							<el-form-item label="传真号码：" prop="leadDept.faxNumber">
						    	<el-input v-model="formData.leadDept.faxNumber" placeholder="请输入"></el-input>
						  	</el-form-item>
						  	<el-form-item label="电子信箱：" prop="leadDept.email">
						    	<el-input v-model="formData.leadDept.email" placeholder="请输入"></el-input>
						  	</el-form-item>
						</div> 	
					</div>
					<h5 class="sub-title mt10"><span class="bar"></span>任务负责人信息</h5>
					<div class="flex-box width-80">
						<div class="flex-item2">
							<div class="flex-box">
								<div class="flex-item1">
									<el-form-item label="姓名：" prop="leadPerson.personName">
								    	<el-input v-model="formData.leadPerson.personName" placeholder="请输入"></el-input>
								  	</el-form-item>
								  	<el-form-item label="职称：" prop="leadPerson.positionTitle">
								    	<el-input v-model="formData.leadPerson.positionTitle" placeholder="请输入"></el-input>
								  	</el-form-item>
								  	<el-form-item label="固定电话：" prop="leadPerson.telPhone">
								    	<el-input v-model="formData.leadPerson.telPhone" placeholder="请输入"></el-input>
								  	</el-form-item>
								</div>
								<div class="flex-item1">
									<el-form-item label="性别：" prop="leadPerson.gender">
								    	<el-input v-model="formData.leadPerson.gender" placeholder="请输入"></el-input>
								  	</el-form-item>
								  	<el-form-item label="最高学历：" prop="leadPerson.education">
								    	<el-input v-model="formData.leadPerson.education" placeholder="请输入"></el-input>
								  	</el-form-item>
								  	<el-form-item label="移动电话：" prop="leadPerson.mobile">
								    	<el-input v-model="formData.leadPerson.mobile" placeholder="请输入"></el-input>
								  	</el-form-item>
								</div>
							</div>
							<div class="flex-box">
								<el-form-item style="display: inline-block;" label="证件类型：" prop="leadPerson.mobile">
							    	<el-select v-model="formData.idCardType" placeholder="请选择">
									    <el-option
									      v-for="item in PRO_CER_TYPE"
									      :key="item.id"
									      :label="item.label"
									      :value="item.id">
									    </el-option>
									</el-select>
							  	</el-form-item>
							  	<el-input class="dt-input flex-item1 max-width-1"  v-model="formData.leadPerson.idCard" style="margin-left: 20px;" placeholder="请输入"></el-input>
							</div>
						</div>
						<div class="flex-item1">
							<el-form-item label="出生日期：">
							    <el-date-picker
							      v-model="formData.leadPerson.birthDate"
							      type="date">
							    </el-date-picker>
							</el-form-item>
							<el-form-item label="从事专业：">
						    	<el-input v-model="formData.leadPerson.major" placeholder="请输入"></el-input>
						  	</el-form-item>
						  	<el-form-item label="电子信箱：">
						    	<el-input v-model="formData.leadPerson.email" placeholder="请输入"></el-input>
						  	</el-form-item>
						  	<el-form-item label="传真号码：">
						    	<el-input v-model="formData.leadPerson.faxNumber" placeholder="请输入"></el-input>
						  	</el-form-item>
						</div> 	
					</div>
					<h5 class="sub-title mt10"><span class="bar"></span>合作国家（地区）机构信息</h5>
					<div class="table-box width-80">
						<dt-table :tableData.sync="formData.coopDeptList" :thead="coopDeptListThead"></dt-table>	
					</div>
				</div>
				<!-- 主要参与人员 -->
				<div v-show="activeIdx === 1">
					<h5 class="sub-title"><span class="bar"></span>主要参与人员（不包括首席科学家，限在编人员和博士后，按所在单位排列）</h5>
					<div class="table-box">
						<dt-table :tableData.sync="formData.mostMemberList"  :thead="mostMemberListThead"></dt-table>						
					</div>
					<div class="table-box" style="margin-top: 30px;">
						<el-table
						    :data="talentData"
						    style="width: 41%;min-width: 400px;"
						    :header-cell-style="{background:'#F9FAFC',color: '#696969',textAlign: 'center'}"
						    >
						    <el-table-column prop="total" label="总人数"></el-table-column>
						    <el-table-column prop="senior" label="高级"></el-table-column>
						    <el-table-column prop="middle" label="中级"></el-table-column>
						    <el-table-column prop="junior" label="初级"></el-table-column>
						    <el-table-column prop="postDoc" label="博士后"></el-table-column>
						</el-table>
					</div>
				</div>
				<!-- 任务内容 -->
				<div v-show="activeIdx === 2">
					<div class="all-fold-box" @click="isFold3 = !isFold3">
						<span v-if="!isFold3"><i class="el-icon-arrow-up"></i>全部收起</span>
						<span v-else><i class="el-icon-arrow-down"></i>全部展开</span>
					</div>
					<h5 class="sub-title"><span class="bar"></span>任务摘要</h5>
					<p class="sub-title-detail">简要说明项目立项的必要性、研究目标、技术方案、预期成果、相关基础条件等（限800字以内）</p>
					<dt-textarea :value.sync="formData.projectAbstract" :isFold="isFold3" :maxNumber="800"></dt-textarea>
		  			<h5 class="sub-title"><span class="bar"></span>立项的必要性</h5>
					<p class="sub-title-detail">阐述立项的必要性以及国内外开展研究及进展情况（限800字以内）</p>
					<dt-textarea :value.sync="formData.approvalNecessay" :isFold="isFold3" :maxNumber="800"></dt-textarea>
		  			
		  			<h5 class="sub-title"><span class="bar"></span>任务研究内容及技术方案</h5>
					<p class="sub-title-detail">（一）主要研究内容（限800字以内）</p>
					<dt-textarea :value.sync="formData.researchContentMain" :isFold="isFold3"  :maxNumber="800"></dt-textarea>
		  			<p class="sub-title-detail">（二）拟采取的研究方法、技术路线及其可行性分析（技术路线可以图表形式显示，限1000字以内</p>
					<dt-textarea :value.sync="formData.researchContentMethod" :isFold="isFold3" :maxNumber="1000"></dt-textarea>
		  			<h5 class="sub-title"><span class="bar"></span>预期取得的重大成果及意义</h5>
					<p class="sub-title-detail">（一）项目成果的呈现形式及描述（限200字以内）</p>
					<dt-textarea :value.sync="formData.achievementForm" :isFold="isFold3" :maxNumber="200"></dt-textarea>
		  			<p class="sub-title-detail">（二）项目成果的预期经济、社会效益（限200字以内）</p>
					<dt-textarea :value.sync="formData.achievementBenefit" :isFold="isFold3" :maxNumber="200"></dt-textarea>
				</div>
				<!-- 任务目标和任务 -->
				<div v-show="activeIdx === 3">
					<h5 class="sub-title"><span class="bar"></span>任务总体目标、考核指标及测评方式/方法</h5>
					<p class="sub-title-detail">限500字以内，应有量化指标，可考核，可比较，可追溯。</p>
					<dt-textarea :value.sync="formData.projectTarget" :maxNumber="500"></dt-textarea>
		  			<h5 class="sub-title"><span class="bar"></span>任务的年度任务、考核指标和时间节点</h5>
					<div class="table-box">
						<dt-table :tableData.sync="formData.taskPreYearList" :thead="taskPreYearListThead"></dt-table>
					</div>
				</div>
				<!-- 经费及预算 -->
				<div v-show="activeIdx === 4">
					<h5 class="sub-title"><span class="bar"></span>任务概算及筹资方案</h5>
					<div class="year-tab mb20">
						<template v-for="(item,idx) in yearOptions">
							<el-button 
								class="dt-btn mb20"
								@click="handleYearClick(item)" 
								:class="activeYear === item ? 'hover':''">{{item}}</el-button>
						</template>
						<span class="dt-square-btn mini" @click="handleAddYear" style="margin-left: 20px;"><i class="el-icon-plus"></i></span>
						<span class="dt-square-btn mini" 
							@click="handleDeleteYear" 
							style="margin-left: 10px;">
							<i class="el-icon-minus"></i>
						</span>
					</div>
					<div class="table-title" v-show="activeYear">{{activeYear||'XXXX'}}年任务年度经费概算</div>
					<table class="count-table" v-for="(item,idx) in formData.budgetPreYearList" v-show="activeYear === item.budgetYear">
						<thead>
							<tr style="border-bottom: 1px solid #FFA39E">
								<th class="td-left">创新工程经费</th>
								<th>金额(万元)</th>
							</tr>
						</thead>
						<tbody>
							<tr class="red-bg">
								<td class="td-left">研究经费总计</td>
								<td>{{item.budgetAmount}}</td>
							</tr>
							<tr v-for="(detail,i) in item.budgetDetail" :key="i">
								<td class="td-left">{{detail.serial +'.'+ detail.name}}</td>
								<td>
									<dt-table-input  @handle-change="handleBugetChange" :rowIdx="idx" :value.sync="detail.amount" :valueRequire="true" type="number"></dt-table-input>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
				<!-- 任务其他信息 -->
				<div v-show="activeIdx === 5">
					<h5 class="sub-title"><span class="bar"></span>基础条件和优势</h5>
					<p class="sub-title-detail">简述前期基础及取得的成果等（限1000字以内）</p>
					<dt-textarea :value.sync="formData.superiorityDeptBasic" :maxNumber="1000"></dt-textarea>
		  			<h5 class="sub-title"><span class="bar"></span>附件</h5>
		  			<div style="margin-bottom: 20px;margin-left: 25px;">
						<!-- <span class="dt-square-btn" @click="handleAdd"><i class="el-icon-plus"></i></span> -->
						<dt-upload-btn 
							:beforeUpload="validateProjectName" 
							styleType="add-icon"
							action="/fileOperator/uploadFile"  
							@upload-success="handleUploadSuccess"></dt-upload-btn>
						<span class="dt-square-btn" 
							@click="handleDelete" 
							style="margin-left: 10px;">
							<i class="el-icon-minus"></i>
						</span>
					</div>
					<div class="table-box">
						<el-table
						    ref="multipleTable"
						    :data="formData.attachmentList"
						    tooltip-effect="light"
						    style="width: 100%;"
						    :header-cell-style="{background:'#F9FAFC',color: '#696969',textAlign: 'center'}"
						    @selection-change="handleSelectionChange">
						    <el-table-column
						      type="selection"
						      width="32">
						    </el-table-column>
						    <el-table-column
						      width="72"
						      prop="serial"
						      label="序号">
						    </el-table-column>
						    <el-table-column
						      prop="attachmentName"
						      show-overflow-tooltip
						      label="附件名称">
						      	<template slot-scope="scope">
						      		<dt-table-input 
						     			:key="scope.$index" 
						     			:rowIdx="scope.$index" 
						     			:value.sync="scope.row.attachmentName" 
						     			:valueRequire="true"
						     			></dt-table-input>
						      	</template>
						    </el-table-column>
						    <el-table-column
						      label="操作"
						      >
						      	<template slot-scope="scope">
						      		<span class="dt-txt-btn">预览</span>
						      		<span class="dt-txt-btn" style="margin-left: 20px;">下载</span>
						      	</template>
						    </el-table-column>
						</el-table>
						<ul class="tips small">
							<li>
								<h6>附件应包括但不限于：</h6>
							</li>
							<li>（一）医学伦理和科研实验室生物安全审查意见（如涉及）</li>
							<li>（二）单位人事部门出具的项目参与人员在职（在站）证明</li>
							<li>（三）近五年承担在研国家科技项目任务书研究内容和考核指标页面复印件（加盖单位科技处章）</li>
							<li>（四）项目申请人员未承担创新工程任务的承诺</li>
						</ul>						
					</div>
				</div>
				<!-- 审核意见 -->
				<div v-show="activeIdx === 6">
					<h5 class="sub-title"><span class="bar"></span>审核意见</h5>
					<div class="table-box">
						<el-table
						    :data="suggestion"
						    tooltip-effect="light"
						    style="width: 100%;"
						    :header-cell-style="{background:'#F9FAFC',color: '#696969',textAlign: 'center'}">
						    <el-table-column
						      prop="label"
						      show-overflow-tooltip
						      label="附件名称">
						    </el-table-column>
						    <el-table-column
						      show-overflow-tooltip
						      label="操作">
						     	<template slot-scope="scope">
						     		<span class="dt-txt-btn" @click="signModelDownload(scope.$index)">模板下载</span>
						     		<dt-upload-btn 
						     			style="margin-left: 20px;" 
						     			styleType="text"
						     			:keyId="scope.$index"
						     			action="/fileOperator/uploadFile" 
						     			:data="{id: formData.id,projectName: formData.projectName}"
						     			@upload-success="handleSignUploadSuccess"
						     			:beforeUpload="validateProjectName"></dt-upload-btn>
						     		<span class="dt-txt-btn" style="margin-left: 20px;" @click="handleSignPreview(scope.$index)">预览</span>
						  		</template>
						    </el-table-column>
						</el-table>						
					</div>
				</div>
			</el-form>
			<!-- 填写说明弹窗 -->
			<dt-dialog key="3" class="large-dialog" :hasContent="true" :isShow="dialogShow" :hasBtn="false" title="填写说明">
		        <div class="dialog-content">
					<ul>
						<li>一、任务申报书由承担单位组织填写，编写人员应客观、真实地填报有关数据，尊重他人知识产权，遵守国家有关知识产权法规。</li>
						<li>二、对于伪造、篡改科学数据，抄袭他人著作、论文或者剽窃他人科研成果等科研不端行为，一经查实，将记入信用记录。</li>
						<li>三、系统自动生成申报书将不改变表格格式和结构。</li>
						<li>四、系统默认正文小4号宋体、1.5倍行距，双面打印，简单装订。</li>
					</ul>
		        </div>
		        <div style="text-align: center;">
		          <el-button class="dt-btn br18 hover" @click="dialogShow = false">关闭</el-button>
		        </div>
	      	</dt-dialog>
	      	<!-- 删除年份 -->
	      	<dt-dialog  key="2" :isShow="dialogShow1" @left-click="handleLeftClick1" @right-click="dialogShow1 = false" :title="'是否删除'+activeYear+'年的数据'"></dt-dialog>
		</div>
	</dt-scroll-box>
	
</template>
<script>
	/**
	 * 任务申请-创新工程-重大协同创新
	 * @author liujingyan 2019-02-15
	 */
	import { mapState } from 'vuex'
	
	//合作国家(地区)机构信息
	const CoopDeptListThead = [{
		label:'单位名称',key: 'deptName',type:'text'
	},{
		label:'任务负责人',key: 'taskLeaderName',type:'text'
	},{
		label:'联系邮箱',key: 'email', valueRequire: false,type:'text'
	}]
	// 主要参与人员
	const MostMemberListThead = [{
		label:'姓名',key: 'personName',type:'text'
	},{
		label:'性别',key: 'gender',type:'select',optionsKey:'SEX'
	},{
		label:'出生年月',key: 'birthDate',type:'date'
	},{
		label:'职称',key: 'positionTitle',type:'text'
	},{
		label:'人员类别',key: 'personCategory',type:'select'
	},{
		label:'学位',key: 'degree',type:'select',optionsKey:'PRO_DEGREE'
	},{
		label:'所在单位',key: 'deptName',type:'text'
	},{
		label:'联系电话',key: 'phone',type:'text'
	},{
		label:'证件号码',key: 'idCard',type:'text'
	},{
		label:'每年工作时间(月)',key: 'workPerYear',type:'text',width:'130px'
	},{
		label:'所属研究任务',key: 'belongTask',type:'text',width:'130px'
	},{
		label:'签字',type:'blank'
	}]
	// 任务的年度任务
	const TaskPreYearListThead = [{
		label:'年度',key: 'taskYear',type:'date',subType:'year'
	},{
		label:'主要任务',key: 'taskName',type:'text'
	},{
		label:'年度考核指标',key: 'taskTargetYear', type:'text'
	},{
		label:'重要任务的时间节点',key: 'importantPoint',type:'date'
	}]

	export default {
		name: 'innovateForerunner',
		data () {
			return {
				tabData:['基本信息','主要参与人员','任务内容','任务目标和任务','经费及预算','任务其他信息','审核意见'],
				activeIdx: 0,
				coopDeptListThead: CoopDeptListThead,
				mostMemberListThead: MostMemberListThead,
				taskPreYearListThead: TaskPreYearListThead,
				dialogShow: false,
				projectNo: 'FG32144657565',
				isFold3: false,
				deadline:[],
				formData:{
					id: '',
					projectName:'',
					applyFunds: '',//
					applyPostNumber: '',
					achievementType:[],
					projectActionDateEnd:'',
					projectActionDateStart:'',
					// 任务承担信息
					leadDept:{
						deptName:'',
						address:'',
						contactsName:'',
						// 联系地址字段暂时无
						// contactsName:'',
						zipCode:'',
						faxNumber:'',
						email:''
					},
					// 任务负责人
					leadPerson:{
						personName:'',
						positionTitle:'',
						telPhone:'',
						gender:'',
						education:'',
						mobile:'',
						idCardType:'',
						idCard:'',
						birthDate:'',
						email:'',
						faxNumber:'',
					},
					// 合作国家(地区)机构信息
					coopDeptList:[],
					// 主要参与人员
					mostMemberList:[],
					projectAbstract:'',//项目摘要
					approvalNecessay:'',//立项的必要性
					researchContentMain:'',
					researchContentMethod:'',
					achievementForm:'',
					achievementBenefit:'',
					projectTarget:'',
					taskPreYearList:[],
					budgetPreYearList:[],
					superiorityDeptBasic:'',
					attachmentList:[],
					attachmentCommittee:{},
					attachmentAudit:{},
					attachmentStatement:{}
				},
				suggestion:[{
						label:'牵头单位学术委员会推荐意见'
					},{
						label:'牵头单位审核意见及承诺'
					},{
						label:'创新工程-重大协同创新申请书声明'
				}],
				talentData:[{
					total: '3412',
					senior: '1231',
					middle: '1241',
					junior: '231',
					postDoc: '5151'
				}],
				multipleSelection: [],
				yearOptions:[],
				activeYear: '',
				dialogShow1: false,
				areaOptions:[{
					id: '1',
					label: '领域1'
				},{
					id: '2',
					label: '领域1'
				},{
					id: '3',
					label: '领域1'
				}],
				rules:{
					projectName:[{required: true, message: '请输入姓名', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'blur'}],
					achievementType:[{type:'array',required: true, message: '请输入拟申请岗位', trigger: 'blur'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
					applyPostNumber:[{required: true, message: '请输入拟申请岗位', trigger: 'submit'}],
				}
			}
		},
		created () {
			// 判断有无项目ID 有项目id为编辑
			let id = this.$route.query.id || ''

			if(id){
				this.getPageData(id)
			}
			if(!this.GlobalLoadingFlag){
				this.dialogShow = true
			}
		},
		mounted () {
			
		},
		methods: {
			// 获取一个年份的预算
			getOneYearBudget (year) {
				let allBudget = []
				this.budgetMenuList.forEach((o)=>{
					allBudget.push({
						serial: o.serial,
						remark: o.remark,
						name: o.name,
						amount: ''
					})
				})

				let item = {
					budgetAmount: 0,
					budgetYear: year || new Date().getFullYear(),
					budgetDetail:[...allBudget],
					serial: this.formData.budgetPreYearList.length					
				}
				this.formData.budgetPreYearList.push(item)
				this.activeYear = item.budgetYear
				this.yearOptions.push(item.budgetYear)

				allBudget = null
			},
			// 执行期限
			handleDeadlineChange (val) {
				if(val){
					this.formData.projectActionDateStart = val[0]
					this.formData.projectActionDateEnd = val[1]
				} else {
					this.formData.projectActionDateStart = ''
					this.formData.projectActionDateEnd = ''
				}
			},
			// 保存数据
			handleSave () {
				if(!this.formData.projectName){
					this.$dt.showMessage('请先填写任务名称','warning')
					return
				}

				this.loading = this.$loading({
		          lock: true,
		          text: '正在保存项目',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        })

				this.$dt.postAjax('/project/apply/innovate/pre',JSON.stringify(this.formData),(res)=>{
						this.loading.close()
						if(!res.code){
							this.$dt.showMessage('保存成功','success')
							this.formData.id = res.projectId
						}
				},(err)=>{
					this.loading.close()
				})
			},
			// 提交数据
			handleSubmit () {
				this.loading = this.$loading({
		          lock: true,
		          text: '正在提交项目',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        })

				this.$dt.postAjax('/project/apply/innovate/pre/submit',JSON.stringify(this.formData),(res)=>{
					this.loading.close()
					// console.log(res)
					this.$dt.showMessage('提交成功','success')
				},()=>{
					this.loading.close()
				})
			},
			getPageData (id) {
				this.$dt.getAjax('/project/apply/innovate/pre/'+ id,{},(res)=>{
					// 预算经费的年份
					if(res.budgetPreYearList && res.budgetPreYearList.length){
						res.budgetPreYearList.forEach((item,idx)=>{
							this.yearOptions.push(item.budgetYear)
							if(idx === 0) {
								this.activeYear = item.budgetYear
							}
						})
					}
					// 项目执行时间
					if(res.projectActionDateEnd){
						this.deadline.push(res.projectActionDateStart)
						this.deadline.push(res.projectActionDateEnd)
					}

					this.formData = {...res}
				},()=>{

				})
			},
			handleTabClick(idx) {
				this.activeIdx = idx
			},
			// 导出项目模板
			exportModel () {

			},
	  		handleSelectionChange (val) {
	  			this.multipleSelection = val
	  		},
	  		handleAddYear () {
	  			let num = null
	  			if(!this.yearOptions.length){
	  				num = new Date().getFullYear()
	  			} else {
	  				num = this.yearOptions[this.yearOptions.length - 1]
	  				num++
	  			}

	  			this.getOneYearBudget(num)
	  			this.$nextTick(()=>{
	  				this.handleYearClick(num)
	  			})
	  		},
	  		handleDeleteYear() {
	  			if(!this.yearOptions.length){
	  				this.$dt.showMessage('没有可删除的年份','warning')
	  				return
	  			}
	  			this.dialogShow1 = true
	  		},
	  		// 预算改变，总额修改
	  		handleBugetChange (rowInfo) {
	  			let amount = this.formData.budgetPreYearList[rowInfo.rowIdx].budgetAmount
	  			let newAmount = amount - Number(rowInfo.value) + Number(rowInfo.newValue)
	  			
	  			this.formData.budgetPreYearList[rowInfo.rowIdx].budgetAmount = newAmount
	  		},
	  		handleYearClick (year) {
	  			this.activeYear = year
	  		},
	  		// 删除年度经费	
	  		handleLeftClick1 () {
	  			let idx = this.yearOptions.findIndex(item => item === this.activeYear)
	  			this.formData.budgetPreYearList.splice(idx,1)
  				this.yearOptions.splice(idx, 1)
  				
  				if(this.yearOptions.length){
  					this.activeYear = this.yearOptions[this.yearOptions.length - 1]
  				} else {
  					this.activeYear = ''
  				}

	  			this.dialogShow1 = false
	  		},
	  		// 附件上传成功
	  		handleUploadSuccess (res) {
	  			let data = res.response.data
	  			if(!this.formData.attachmentList){
	  				this.formData.attachmentList = []
	  			}
	  			this.formData.attachmentList.push({
	  				id: data.id,
	  				projectId: this.formData.id,
	  				fileType:  data.fileExt,
	  				attachmentName: data.fileName,
	  				fileUrl: data.fileUrl,
	  				attachmentRemark: ''
	  			})
	  		},
	  		// 审核意见文件上传
	  		handleSignUploadSuccess(res){
	  			let idx = res.key
	  			let data = res.response.data
	  			let item = {
	  				id: data.id,
	  				projectId: this.formData.id,
	  				fileType:  data.fileExt,
	  				attachmentName: data.fileName,
	  				fileUrl: data.fileUrl,
	  				attachmentRemark: '',
	  				attachmentCategory: '01'
	  			}

	  			switch(idx){
	  				case 0:
	  					item.attachmentCategory = 'attachmentCommittee'
	  					this.formData.attachmentCommittee = {...item}
	  					break
	  				case 1:
	  					item.attachmentCategory = 'attachmentAudit'
	  					this.formData.attachmentAudit = {...item}
	  					break
	  				case 2:
	  					item.attachmentCategory = 'attachmentStatement'
	  					this.formData.attachmentStatement = {...item}
	  					break
	  			}
	  		},
	  		// 下载审核意见模板
			signModelDownload (idx) {
				console.log(idx)
			},
			// 审核意见预览
			handleSignPreview (idx) {
				switch(idx){
					case 0:
						if (!this.formData.attachmentCommittee) {
							this.$dt.showMessage('您还未上传附件','warning')
							return
						}
						window.open(this.formData.attachmentCommittee.fileUrl, "_blank")
						break
					case 1:
						if (!this.formData.attachmentAudit) {
							this.$dt.showMessage('您还未上传附件','warning')
							return
						}
						window.open(this.formData.attachmentAudit.fileUrl, "_blank")
						break
					case 2:
						if (!this.formData.attachmentStatement) {
							this.$dt.showMessage('您还未上传附件','warning')
							return
						}
						window.open(this.formData.attachmentStatement.fileUrl, "_blank")
						break
				}
			},
	  		validateProjectName () {
	  			if(!this.formData.projectName){
	  				this.$dt.showMessage('请先填写任务名称','warning')
	  				return false
	  			}

	  			return true
	  		},
	  		// 删除附件
	  		handleDelete () {

			},
			handleFillBudget () {
				if(!this.validateProjectName()){
					return
				} 
				if(!this.formData.id){
					this.$dt.showMessage('请先保存任务！','warning')
					return
				}

				this.$router.push({
					name: 'innovateBudget',
					query:{
						id: this.formData.id || '',
						type: 'forerunner'
					}
				})
			}
		},
		computed:{
			...mapState([
				'GlobalLoadingFlag',
				'PRO_RELUST_TYPE',
				'PRO_DEGREE',
				'PRO_CER_TYPE'
			]),
			totalOutlay () {
				return Number(this.formData.fundSourceProject) + Number(this.formData.fundSourceSelf) + Number(this.formData.fundSourceOther)
			},
			budgetMenuList() {
				let data = this.$store.state.BUDGET_SUBJECT
				if(data.length){
					data = JSON.parse(data[0].label)
					data.forEach((item)=>{
						item.amount = ''
					})
				}
				return  data
			}
		},
		watch:{
			'GlobalLoadingFlag': function (val) {
				if(val === false){
					this.$nextTick(()=>{
						this.dialogShow = true
					})
				}
			}
		}
	}
</script>
